import Link from "next/link";

class Header extends React.Component {
  render() {
    return (
      <Link>
        <a>Navbar</a>
        <style jsx>{`
          a {
            text-decoration: none;
            color: blue;
          }

          a:hover {
            opacity: 0.6;
          }
        `}</style>
      </Link>
    );
  }
}

export default Header;



